import React, { PureComponent } from "react";
import { func } from "prop-types";
import throttle from "lodash/throttle";

import OptionSlider from "./stateless/OptionSlider";
import OptionColorPicker from "./stateless/OptionColorPicker";
import * as vectors from "../../../utils/vectors.constant";

export default class Controls extends PureComponent {
  static propTypes = {
    updateOptions: func.isRequired,
    loadVector: func.isRequired,
    onReset: func.isRequired
  };

  delayedChange = throttle(options => {
    this.props.updateOptions(options);
  }, 50);

  constructor(props) {
    super(props);
    this.state = {
      showColorPicker: false,
      showPulseColorPicker: false
    };
  }

  closeDropdown(id) {
    const capitalized = id[0].toUpperCase() + id.slice(1);
    const property = `show${capitalized}Picker`;
    this.setState(prev => ({ [property]: false }));
  }

  toggleDropdown(id) {
    if (id === "color") {
      this.setState(prev => ({
        showColorPicker: !prev.showColorPicker,
        showPulseColorPicker: false
      }));
    } else if (id === "pulseColor") {
      this.setState(prev => ({
        showColorPicker: false,
        showPulseColorPicker: !prev.showPulseColorPicker
      }));
    }
  }

  render() {
    const delayedChange = this.delayedChange.bind(this);
    const { options, onReset } = this.props;
    const { showColorPicker, showPulseColorPicker } = this.state;

    return (
      <div>
        <span className="subtitle">
          <span className="icon">
            <i className="fa fa-cogs" />
          </span>{" "}
          Options
        </span>
        <a className="button is-small is-pulled-right" onClick={onReset}>
          <span className="icon is-small">
            <i className="fa fa-reply" />
          </span>
          <span>reset</span>
        </a>
        <hr />
        <div className="field">
          {["paused", "reverse", "hardwareAccelerated"].map(opt => (
            <div className="control is-inline" key={opt}>
              <label className="checkbox">
                <input
                  type="checkbox"
                  checked={options[opt]}
                  onChange={ev => delayedChange({ [opt]: ev.target.checked })}
                />
                <small>
                  <b> {opt}</b>
                </small>
              </label>
            </div>
          ))}
        </div>
        <div className="field">
          <div className="control has-icons-left">
            <div className="select is-rounded">
              <select
                onChange={ev => {
                  const use = ev.target.value;
                  if (use === vectors.CIRCLE) {
                    this.props.updateOptions({ ...options, use, radius: 300 });
                  } else {
                    delete options.radius;
                    this.props.updateOptions({ ...options, use });
                  }
                  this.props.loadVector(use);
                }}
              >
                {Object.values(vectors).map(vector => (
                  <option key={vector} value={vector}>
                    L.{vector}
                  </option>
                ))}
              </select>
            </div>
            <div className="icon is-small is-left">
              <i className="fa fa-exchange" />
            </div>
          </div>
        </div>

        <OptionSlider
          property="delay"
          onOptionChange={delay => delayedChange({ delay })}
          min={1}
          max={800}
          option={options.delay}
        />

        <OptionSlider
          property="weight"
          onOptionChange={weight => delayedChange({ weight })}
          min={1}
          max={20}
          option={options.weight}
        />

        {options.use === vectors.CIRCLE ? (
          <OptionSlider
            property="radius"
            onOptionChange={radius => delayedChange({ radius })}
            min={100}
            max={800}
            option={options.radius}
          />
        ) : null}

        <div className="columns">
          <div className="column is-6">
            <OptionSlider
              property="dashArray[0]"
              onOptionChange={dashArrayX =>
                delayedChange({ dashArray: [dashArrayX, options.dashArray[1]] })
              }
              min={1}
              max={100}
              option={options.dashArray[0]}
            />
          </div>

          <div className="column is-6">
            <OptionSlider
              property="dashArray[1]"
              onOptionChange={dashArrayY =>
                delayedChange({ dashArray: [options.dashArray[0], dashArrayY] })
              }
              min={1}
              max={100}
              option={options.dashArray[1]}
            />
          </div>
        </div>
        <div className="columns is-mobile">
          <div className="column is-centered is-6">
            <OptionColorPicker
              onClick={() => this.toggleDropdown("color")}
              onBlur={() => this.closeDropdown("color")}
              display={showColorPicker}
              property="color"
              option={options.color}
              onChange={color => delayedChange({ color })}
            />
          </div>
          <div className="column is-centered is-6">
            <OptionColorPicker
              onClick={() => this.toggleDropdown("pulseColor")}
              display={showPulseColorPicker}
              property="pulseColor"
              option={options.pulseColor}
              align="right"
              onBlur={() => this.closeDropdown("pulseColor")}
              onChange={pulseColor => delayedChange({ pulseColor })}
            />
          </div>
        </div>
      </div>
    );
  }
}
